.hero {
  position: relative;
  background: url("../img/hero-back.png") center;
  background-repeat: no-repeat;
  background-size: cover;
  // background-attachment: fixed;

  &::after {
    width: 100%;
    height: 100%;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(2, 2, 4, 0.55);
  }

  &__container {
  }

  &__wrapper {
    min-height: 720px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;

    @media (max-width: 380px) {
    min-height: 600px;
    }
  }

  &__text {
    max-width: 610px;
  }

  &__title {
    margin: 0;
    padding-bottom: 35px;

    font-family: var(--font-family);
    font-weight: 900;
    font-size: 60px;
    line-height: 144%;
    text-transform: uppercase;
    color: #fff;

    @include small-tablet {
      font-size: 45px;
    }
  }

  &__info {
    margin: 0;
    padding-bottom: 35px;

    max-width: 535px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    color: #dfdfdf;

    @include mobile {
      font-size: 16px;
    }
  }

  &__text-inner {
    display: flex;
    gap: 20px;

    @media (max-width: 450px) {
      flex-direction: column;
    }
  }

  &__btn {
    max-width: 200px;

    border-radius: 10px;

    font-weight: 500;
    font-size: 15px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #fff;
  }

  &__btn-buy {
    padding: 17px 55px;

    background-color: #c7a17a;

    transition: 0.3s ease-in-out;

    @include hover {
      background-color: #a88666;
    }
  }

  &__btn-read {
    padding: 17px 45px;

    border: 1px solid #666681;

    transition: 0.3s ease-in-out;

    @include hover {
      background-color: #666681;
    }
  }
}
