.news {
  &__container {
  }

  &__wrapper {
  }

  &__item {
    max-width: 386px;

    display: flex;
    flex-direction: column;

    border-radius: 10px;
    background-color: #14141d;
  }

  &__image {
  }

  &__text {
    padding: 20px 21px;
  }

  &__name {
  }

  &__info {
    margin: 0;
    padding-top: 13px;

    font-weight: 400;
    font-size: 16px;
    color: #dfdfdf;
  }
}

.swiper {
  padding-bottom: 30px !important;
  @media (max-width: 768px) {
    width: 100%;
    height: 100%;
  }
}

.swiper-slide {
  width: 386px;
  height: auto;

  @media (max-width: 768px) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.swiper-pagination-bullet {
  background: white;
}
