.footer {
  &__container {
    display: flex;
    justify-content: flex-start;

    @media (max-width: 550px) {
      flex-direction: column;
      gap: 25px;
    }
  }

  &__left {
    width: 35%;

    @media (max-width: 830px) {
      width: 50%;
    }

    @media (max-width: 550px) {
      width: 100%;
    }
  }

  &__logo {
    display: flex;
    align-items: center;

    font-weight: 900;
    font-size: 20px;
    text-transform: uppercase;
    color: #f8dabf;

    @media (max-width: 550px) {
      justify-content: center;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @media (max-width: 550px) {
      width: 100%;

      gap: 15px;

      text-align: center;
    }
  }

  &__list-first {
    width: 35%;

    @media (max-width: 550px) {
      width: 100%;
    }
  }

  &__item {
  }

  &__link {
    font-weight: 600;
    font-size: 15px;
    text-transform: uppercase;
    color: #fff;

    transition: 0.3s ease-in-out;

    @include hover {
      color: #c7a17a;
    }
  }

  &__list-second {
    width: 32%;

    @media (max-width: 550px) {
      width: 100%;
    }
  }
}

.active {
  color: #c7a17a;
}
