.catalog {
  &__container {
  }

  &__subtitle {
    @media (max-width: 1229px) {
      text-align: center;
    }
  }

  &__list {
    width: 100%;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    gap: 21px;
  }

  &__item {
  }

  &__card {
    min-width: 386px;
    width: 100%;
    height: 100%;

    padding: 20px;

    display: flex;
    flex-direction: column;
    cursor: pointer;
    background-color: #14141d;
    border-radius: 10px;

    transition: 0.3s ease-in-out;

    @include hover {
      background: #22222f;
    }

    @media (max-width: 860px) {
      min-width: 300px;
    }

    @media (max-width: 660px) {
      min-width: 280px;
    }
  }

  &__image {
    max-width: 100px;
    max-height: 200px;
    margin: 0 auto;

    flex: 1 0 auto;
  }
}

.card {
  &__textblock {
    padding-top: 42px;

    display: flex;
    flex-direction: column;
    flex: 1;
  }

  &__name {
  }

  &__bottom {
    margin-top: auto;
    padding-top: 30px;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__price {
    font-weight: 400;
    font-size: 18px;
    text-transform: uppercase;
    color: #fff;
  }

  &__price-del {
    margin-left: 30px;

    text-decoration: line-through;
    color: #818181;
  }

  &__svg {
  }
}
