.header {
  &__content {
    position: relative;

    @include flex-all-sb;

    padding: 36px 0;

    @media (max-width: 968px) {
      justify-content: flex-end;
    }
  }

  &__container {
  }

  &__hr {
    margin: 0;

    border: 1px solid #32323d;
  }

  &__nav {
    @media (max-width: 968px) {
      position: fixed;
      top: 0;
      left: -100%;
      overflow-y: auto;
      z-index: 100;
      width: 80%;
      max-width: 375px;
      height: 100vh;
      background-color: #020204;
      transition: opacity 0.3s, visibility 0.3s, transform 0.3s, left 0.3s;
      box-shadow: 10px 0 23px -5px rgba(34, 60, 80, 0.2);
    }
  }

  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (max-width: 968px) {
      left: 0;
      transform: translate(0%, -50%);
    }
  }

  &__list {
    display: flex;
    gap: 30px;
  }

  &__item {
  }

  &__link {
  }

  &__link-cart {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: -13px;
      left: 15px;
      display: block;
      width: 6px;
      height: 6px;

      border-radius: 100%;
      background-color: #c7a17a;
    }
  }
}

.menu--active {
  left: 0;
}

.lang {
  &__content {
    @include flex-all-sb;

    padding: 5px 0;
  }

  &__right {
  }

  &__btn {
    font-weight: 600;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;

    transition: 0.3s ease-in-out;

    @include hover {
      color: #c7a17a;
    }
  }

  &__left {
  }

  &__phone {
    font-weight: 600;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
  }
}

.nav {
  &__list {
    display: flex;
    gap: 35px;

    @media (max-width: 968px) {
      padding: 50px;

      display: flex;
      flex-direction: column;
      gap: 30px;
    }
  }

  &__item {
  }

  &__link {
    font-weight: 600;
    font-size: 15px;
    text-transform: uppercase;
    color: #fff;
    transition: 0.3s ease-in-out;

    @include hover {
      color: #c7a17a;
    }
  }
}

.active {
  color: #c7a17a;
}
