.post {
  padding-top: 100px;
  padding-bottom: 40px;

  @include mobile {
    padding-top: 40px;
  }

  &__container {
  }

  &__card {
    display: flex;
    align-items: center;
    gap: 29px;

    border-radius: 10px;
    background-color: #14141d;

    @media (max-width: 1080px) {
      flex-direction: column;
      justify-content: center;
      background-color: #020204;
    }
  }

  &__image {
    border-radius: 10px 0px 0px 10px;

    filter: brightness(50%);

    @media (max-width: 1080px) {
      order: 1;
      border-radius: 10px;
    }
  }
}
.card {
  &__text {
    max-width: 500px;

    padding: 30px 20px 40px 0;

    @media (max-width: 1080px) {
      padding: 0;

    }
  }

  &__subtitle {
    padding-bottom: 15px;

    max-width: 250px;
  }

  &__descr {
    margin: 0;

    font-weight: 400;
    font-size: 18px;
    color: #dfdfdf;

    @include mobile {
      font-size: 15px;
    }
  }
}
